// export default AboutPage

export default function About() {
  return (
    <StaticQuery
      query={graphql`
        query {
          allContentfulThingsILove {
            edges {
              node {
                picture {
                  file {
                    url
                  }
                  fixed(width: 10, height: 10) {
                    base64
                    tracedSVG
                    aspectRatio
                    srcWebp
                    srcSetWebp
                  }
                }
                title
                body
              }
            }
          }
        }
      `}
      render={data => (
        <div>
          <div>
            <h2 className={projectsStyle.title}>Things I love</h2>
            <div className={aboutStyle.cardContainer}>
              <ol>
                {data.allContentfulThingsILove.edges.map(edge => {
                  return (
                    <div className={aboutStyle.cardWrapper}>
                      <div className={aboutStyle.content}>
                        <div className={aboutStyle.up}>
                          <div className={aboutStyle.circle}>
                            <div className={aboutStyle.imgWrapper}>
                              <img
                                className={aboutStyle.img}
                                src={edge.node.picture.file.url}
                                alt={edge.node.title}
                              ></img>
                            </div>
                          </div>
                          <h3>{edge.node.title}</h3>
                        </div>
                        <div className={aboutStyle.down}>
                          {" "}
                          <p>{edge.node.body}</p>{" "}
                        </div>
                      </div>
                    </div>
                  )
                })}
              </ol>
            </div>
          </div>
          <div className={aboutStyle.wrapper} id="about">
            <h2 className={projectsStyle.title}>ABOUT</h2>
            <p className={aboutStyle.body}>
              My name is Sooyoung Moon. I’m a computer science student at the
              University of Arizona. I enjoy exploring good places, listening to
              music, and programming something that can help myself and people.
            </p>
            <p className={aboutStyle.body}>
              I'm currently working for YesPlz AI as a part-time intern, taking
              a math class at the University of Arizona, and being trained at
              the Engineering Essentials program by Goldman Sachs.
            </p>
            <p className={aboutStyle.body}>
              In 2017, I graduated with a degree in biology and worked at a
              neuroscience laboratory. Back in that time, I realized that my
              passion lied in combining neuroscience with computer science and
              moving into a cutting-edge technology. It just so happened that I
              saw a massive hype on AI. Since then, I had studied deep learning
              on my own and started at the University of Arizona to strengthen
              the basics of computer science.
            </p>
            <p className={aboutStyle.body}>
              Now I am passionately open to learning new materials and broadly
              interested in web development, data visualization, crawling as
              well as deep learning. Whether you feel like learning more about
              me, working together, or just wanting to say hi, feel free to
              shoot me a message.
            </p>
          </div>
        </div>
      )}
    />
  )
} 
